import styled from "styled-components";

export const StyledContainer = styled.div`
    display: flex;
    height: 40px;
    align-items: center;
`;
export const StyledImg = styled.img`
    height: 45px;
    margin: 0 10px;
`;
export const StyledH1 = styled.h1`
    color: #fff;
    font-family: "Lobster", cursive;
    font-size: 18pt;

    @media (max-width: 700px) {
        display: none;
    }
`;
